
import {defineComponent, reactive, toRefs, SetupContext} from 'vue'

interface Props {

}

interface Data {
  visible: boolean
}

export default defineComponent({
  name: "NoticeIcon",
  components: {},
  props: {
    count: {
      type: [Number, String],
      default: 0
    },
    placement: {
      type: String,
      default: 'top'
    }
  },
  setup(props: Props, ctx: SetupContext) {
    let data: Data = reactive<Data>({
      visible: false
    })
    return {
      ...toRefs(data)
    }
  }
})
