
import {defineComponent, reactive, toRefs, SetupContext} from 'vue'

interface Props {

}

interface Data {

}

export default defineComponent({
  name: "Trend",
  components: {},
  props: {
    type: {
      type: String,
      default: 'up'
    },
    isReserve: {
      type: Boolean,
      default: false
    }
  },
  setup(props: Props, ctx: SetupContext) {
    let data: Data = reactive<Data>({})
    return {
      ...toRefs(data)
    }
  }
})
