
import {defineComponent, reactive, toRefs, SetupContext} from "vue"
import clickoutside from '@/utils'

interface Data {
  flag: boolean,
  scale: number,
  deg: number,
  activeIndex: number,
}

export default defineComponent({
  name: "Preview",
  components: {},
  emits: ['clickPreviewImg', 'clickPreviewImgs'],
  props: {
    imgUrl: {
      type: [String, Array]
    },
    isMark: {
      type: Boolean,
      default: true
    }
  },
  setup(props: any, ctx: SetupContext) {
    const data: Data = reactive<Data>({
      flag: false,
      scale: 1,
      deg: 0,
      activeIndex: 0
    })
    // 点击预览
    const preview = () => {
      data.flag = !data.flag
    }
    // 点击关闭
    const close = () => {
      data.flag = !data.flag
      data.scale = 1
      data.deg = 0
      data.activeIndex = 0
    }
    // 放大
    const magnify = () => {
      data.scale += 0.1
      if (data.scale > 3) data.scale = 3
    }
    // 缩小
    const shrink = () => {
      data.scale -= 0.1
      if (data.scale < 0.1) data.scale = 0.1
    }
    // 顺时针
    const clockwise = () => {
      data.deg += 90
      if (data.deg === 360) data.deg = 0
    }
    // 逆时针
    const anticlockwise = () => {
      data.deg -= 90
      if (data.deg === -360) data.deg = 0
    }
    // 点击单个预览图片
    const clickPreviewImg = () => {
      ctx.emit('clickPreviewImg')
      data.flag = false
    }
    // 点击多个预览图片
    const clickPreviewImgs = (item: string) => {
      ctx.emit('clickPreviewImgs', item)
      data.flag = false
    }
    // 点击多图预览的预览图片
    const previewImgs = (item: string, index: number) => {
      data.activeIndex = index
      data.flag = true
    }
    // 预览右键
    const right = () => {
      data.activeIndex++
      if (data.activeIndex === props.imgUrl.length) {
        data.activeIndex = 0
      }
    }
    // 预览左键
    const left = () => {
      data.activeIndex--
      if (data.activeIndex === -1) {
        data.activeIndex = props.imgUrl.length - 1
      }
    }
    // 点击遮罩层关闭
    const markClose = () => {
      if (props.isMark) data.flag = false
    }
    return {
      ...toRefs(data),
      shrink,
      magnify,
      clockwise,
      anticlockwise,
      close,
      preview,
      clickPreviewImg,
      previewImgs,
      clickPreviewImgs,
      right,
      left,
      markClose
    }
  }
})
