
import {defineComponent, reactive, toRefs, SetupContext, onMounted} from 'vue'


interface Data {
  time: number,
  text: string
}

export default defineComponent({
  name: "CountDown",
  components: {},
  props: {
    time: {
      type: Number,
      default: 60
    },
    text: {
      type: String,
      default: '发送验证码'
    },
    sendText: {
      type: String,
      default: '后重新发送'
    },
    endText: {
      type: String,
      default: '重新发送'
    }
  },
  setup(props, ctx: SetupContext) {
    let data: Data = reactive<Data>({
      text: '',
      time: 0
    })
    let handleClick = () => {
      let timer = setInterval(() => {
        data.time--
        data.text = `${data.time}s${props.sendText}`
        if (data.time === 0) {
          clearInterval(timer)
          data.text = props.endText
          data.time = props.time
        }
      }, 1000)
    }
    onMounted(() => {
      data.text = props.text
      data.time = props.time
    })
    return {
      ...toRefs(data),
      handleClick
    }
  }
})
