
import {defineComponent, reactive, toRefs, SetupContext, ref, nextTick, watch} from 'vue'
interface Data {
  flag: boolean
}

export default defineComponent({
  name: "EditText",
  components: {},
  props: {
    text: {
      type: String,
      required: true
    }
  },
  setup(props, ctx: SetupContext) {
    let data: Data = reactive<Data>({
      flag: false
    })
    let iptRef = ref<any>(null)
    let handleClick = () => {
      data.flag = true
      nextTick(() => {
        iptRef.value.focus()
      })
    }
    let handBlur = () => {
      setTimeout(() => {
        data.flag = false
      }, 80)
    }
    let ok = () => {
      ctx.emit('ok')
      data.flag = false
    }
    let cancel = () => {
      ctx.emit('cancel')
      data.flag = false
    }
    return {
      ...toRefs(data),
      handleClick,
      handBlur,
      ok,
      cancel,
      iptRef
    }
  }
})
